* {
  box-sizing: border-box;
}

h3, p {
  margin: 0;
}

.page {
  font-family: Roboto, sans-serif;
}

.page__body {
  margin: 0;
}

.page__main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .page__main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .page__main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .page__main {
    grid-template-columns: repeat(4, 200px);
  }
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-inline: 50px;
  font-size: 12px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 100%;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color, all .3s;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link--active {
  color: #00acdc;
  position: relative;
}

.nav__link--active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  padding-inline: 16px;
  transition: transform, color, all .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  align-self: center;
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__stars-blok {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  text-align: right;
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-number {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 0 solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 12px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: border, background-color, color, all .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #0000;
  border: 1px solid #00acdc;
}

.stars {
  margin-bottom: 0;
  display: flex;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: block;
}

.stars__star:last-child {
  margin-right: 0;
}

